"use client";

import { useEffect, useState } from "react";
import UploadReel from "./UploadReel";

export default function ReelsFeed() {
  const [reels, setReels] = useState([]);

  const fetchReels = async () => {
    try {
      const res = await fetch("/api/reels/random");
      const data = await res.json();
      setReels(data.reels || []);
    } catch (err) {
      console.error(err);
    }
  };

  useEffect(() => {
    fetchReels();
  }, []);

  return (
    <div style={{ marginTop: "20px" }}>
      <UploadReel onUpload={fetchReels} />

      <h2>Reels</h2>
      {reels.length === 0 ? (
        <p>No reels yet</p>
      ) : (
        <div style={{ display: "flex", flexWrap: "wrap", gap: "10px" }}>
          {reels.map((r) => (
            <div key={r.id} style={{ width: "200px", border: "1px solid #ccc", borderRadius: "8px", padding: "5px" }}>
              <video src={r.video} controls style={{ width: "100%", borderRadius: "6px" }} />
              {r.caption && <p style={{ marginTop: "5px" }}>{r.caption}</p>}
              <small>By: {r.author?.name || "Unknown"}</small>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}
